

































import { Component, Prop, Vue } from 'vue-property-decorator';
import MainPageHeader from '../components/MainPageHeader.vue';
import BigButton from '../components/input/BigButton.vue';
import AuthButton from '../components/input/AuthButton.vue';
import DefaultInputField from '../components/input/DefaultInputField.vue';
import PopupBackground from '../components/PopupBackground.vue';
import { getAuthService } from '../services/authService';

@Component({
    name: 'LoginPage',
    components: {
        MainPageHeader,
        BigButton,
        AuthButton,
        DefaultInputField,
        PopupBackground,
    },
    data() {
        return {

        };
    },
    methods: {
        
    }
})
export default class LoginPage extends Vue {
    private email: string = '';
    private password: string = '';
    private rememberMe: boolean = false;
    private tabKey: string|null = null;

    async doLogin() {
        this.tabKey = this.$route.params.tabKey;
        const loggedIn = await getAuthService().logIn(this.email, this.password);

        if (loggedIn) {
            const role = await getAuthService().getRole();
            if (this.tabKey != null) {
                this.$router.push({ name: 'summaryPage', params: { 'tabKey': this.tabKey } });
            } else {
                this.$router.push({ name: 'mainPage' });
            }
        }
    }
}
